body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width:100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background-image-container {
    background-image:url(/999galk/EventsFinderApp/static/media/headerImg.0f40f473.jpg);
    position: relative;
    text-align: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    width:100%;
    height:100%;
    
}
.placeholder {
	height:400px;
}
header {
	width: 100%;
  	position: relative;
}
@media screen and (min-width: 400px) {
  #bar {
    background-color: transparent;
    box-shadow: none;
  }
}
@media screen and (min-width: 800px) {
  #bar {
    background-color: #24478f;
    }
}
.rbc-event-content{
  position: relative;
}
.singleEvent{
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  z-index:99;
  top:0
}
.hidden {
  display: none !important;
}
.back {
  z-index: -1;
}
#details {
 color:white;
 background-color: black;
}
.eventDetails{
  background-color: black;
}
.rbc-calendar{
  padding:15px;
  margin-left:7%;
  margin-right:7%;
  border : solid #3174ad;
  border-radius: 1.5rem;
}
#calendar{
  background-color: #F1F2F7;
}
html,body{
  height: 100%;
}
.App {
  text-align: center;
  position:fixed;
    padding:0;
    margin:0;

    top:0;
    left:0;
    width: 100%;
    height: 100%;
    overflow: auto;
}

footer{
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin-bottom: 0px;
}
